"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import api from "@/lib/api";
import Form from "@/components/form";
import { toast } from "react-hot-toast";

export default function ClienteForm({ clienteId }) {
  const router = useRouter();
  const isEditing = Boolean(clienteId);

  const [cliente, setCliente] = useState(() => 
    isEditing
      ? { codigo: "", nome: "", fantasia: "", documento: "", endereco: "" }
      : { nome: "", fantasia: "", documento: "", endereco: "" }
  );

  useEffect(() => {
    if (isEditing) {
      api.get(`/clientes/${clienteId}`)
        .then((response) => setCliente(response.data))
        .catch(() => toast.error("Erro ao buscar cliente"));
    }
  }, [clienteId, isEditing]);

  const handleChange = (e) => {
    setCliente({ ...cliente, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("token");
      if (!token) {
        toast.error("Usuário não autenticado");
        return;
      }

      const cleanedCpf = cliente.documento.replace(/\D/g, "");

      const payload = { ...cliente, documento: cleanedCpf, usuarioNome: "-" };
      const config = { headers: { Authorization: `Bearer ${token}` } };

      if (isEditing) {
        await api.put(`/clientes/${clienteId}`, payload, config);
        toast.success("Cliente atualizado com sucesso!");
      } else {
        await api.post("/clientes", payload, config);
        toast.success("Cliente cadastrado com sucesso!");
      }

      router.push("/clientes");
    } catch (error) {
      if (error.response) {
        const { status, data } = error.response;

        if (status === 409) {
          if (data.includes("código")) {
            toast.error("Já existe um cliente com esse código.");
          } else if (data.includes("documento")) {
            toast.error("Já existe um cliente com esse documento.");
          } else {
            toast.error("Conflito ao salvar cliente.");
          }
        } else if (status === 400) {
          toast.error("Dados inválidos. Verifique os campos preenchidos.");
        } else if (status === 401) {
          toast.error("Usuário não autenticado.");
          router.push("/");
        } else {
          toast.error(`Erro desconhecido: ${data}`);
        }
      } else {
        console.error("Erro ao salvar cliente:", error);
        toast.error("Erro ao salvar cliente. Tente novamente.");
      }
    }
  };

  const fields = [
    ...(isEditing ? [{ name: "codigo", label: "Código", required: true, type: "number", disabled: true }] : []),
    { name: "nome", label: "Nome", required: true },
    { name: "fantasia", label: "Nome Fantasia", required: true },
    { name: "documento", label: "Documento (CPF)", required: true },
    { name: "endereco", label: "Endereço", required: true },
  ];
  

  return (
    <>
      
    </>
  );
}
